section {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
    flex-wrap: wrap;
    overflow-y: scroll;
}

.articleLink {
    cursor: pointer;
}

.newsCont {
    padding: 5px;
    margin: 25px;
    width: 400px;
    height: 250px;
    border: 1px solid black;
    border-radius: 10px;
    box-shadow: -5px 0px 10px rgba(0, 0, 0, .5), 5px 5px 10px rgba(0, 0, 0, .5);
    overflow:hidden;
}

.newsPic {
    border-radius: 5px;
    width: 100%;
    height: 75%;
}

.articleTitle {
    width: 90%;
    overflow:hidden;
    text-align: left;
    margin: 5px;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.articleAuthor {
    text-align: left;
    margin-left: 10px;
}